.megabundle {
    display: flex;
    flex-direction: column;

    div {
        font-size: 18px;

        span {
            font-size: 15px;
        }
    }
}

.hasMegaBundle {
    color: green !important;
}

.noMegaBundle {
    color: red !important;
}

.table-wrapper {
    margin: 10px 70px 30px;
    box-shadow: 0px 35px 50px rgba(0, 0, 0, 0.2);
}

.fl-table {
    border-radius: 5px;
    // font-size: 12px;
    font-weight: normal;
    border: none;
    border-collapse: collapse;
    width: 100%;
    max-width: 100%;
    white-space: nowrap;
    background-color: white;
}

.fl-table td,
.fl-table th {
    text-align: center;
    padding: 8px;
}

.fl-table td {
    font-size: 20px;
    color: rgb(54, 54, 54);
    align-items: center;
    text-align: center;

    span {
        color: rgb(125, 125, 125);
        font-size: 15px;

    }

    a {
        text-decoration: none;
        color: rgb(54, 54, 54);

        &:hover {
            color: rgb(167, 166, 166);

        }

    }
}


.fl-table thead th {
    color: #ffffff;
    background: rgba(9, 10, 17, 0.8);
    font-size: 22.5px;
    padding-left: 50px;
    padding-right: 50px;
}



.fl-table thead th:nth-child(odd) {
    color: #ffffff;
    background: #252525;
}

.fl-table thead th:nth-child(1) {
    border-top-left-radius: 5px;
}
.fl-table thead th:last-child {
    border-top-right-radius: 5px;
}

.fl-table tr:nth-child(even) {
    background: #F8F8F8;
}

/* Responsive */

@media (max-width: 767px) {
    .fl-table {
        display: block;
        width: 100%;
    }

    .table-wrapper:before {
        content: "Scroll horizontally >";
        display: block;
        text-align: right;
        font-size: 11px;
        color: white;
        padding: 0 0 10px;
    }

    .fl-table thead,
    .fl-table tbody,
    .fl-table thead th {
        display: block;
    }

    .fl-table thead th:last-child {
        border-bottom: none;
    }

    .fl-table thead {
        float: left;
    }

    .fl-table tbody {
        width: auto;
        position: relative;
        overflow-x: auto;
    }

    .fl-table td,
    .fl-table th {
        padding: 20px .625em .625em .625em;
        height: 60px;
        vertical-align: middle;
        box-sizing: border-box;
        overflow-x: hidden;
        overflow-y: auto;
        width: 120px;
        // font-size: 13px;
        text-overflow: ellipsis;
    }

    .fl-table thead th {
        text-align: left;
        border-bottom: 1px solid #f7f7f9;
    }

    .fl-table tbody tr {
        display: table-cell;
    }

    .fl-table tbody tr:nth-child(odd) {
        background: none;
    }

    .fl-table tr:nth-child(even) {
        background: transparent;
    }

    .fl-table tr td:nth-child(odd) {
        background: #F8F8F8;
        border-right: 1px solid #E6E4E4;
    }

    .fl-table tr td:nth-child(even) {
        border-right: 1px solid #E6E4E4;
    }

    .fl-table tbody td {
        display: block;
        text-align: center;
    }
}


.block-link {
    font-size: 90%;
    color: rgb(125, 125, 125);
    margin-right: .5%;

}